import React from 'react';
import Home from './Home';
import About from './About';
import Services from './Services';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
const menuItems = [{ 'name': 'MyBlog', 'url': '/MyBlog','key':'a' },
{ 'name': 'about', 'url': '/MyBlog/about','key':'b' },
{ 'name': 'services', 'url': '/MyBlog/services','key':'c' }];
class Header extends React.Component {

    constructor(props) {
        super(props);
        this.state = { name: 'MyBlog' };    
    }

    componentDidMount(){
        let stateNames = window.location.pathname.split('/');
        let stateName = stateNames[stateNames.length-1];
        this.setState({name:stateName===''?'MyBlog':stateName});
    }

    _handleMenuClick = (menu) => {
        this.setState({ name: menu });
    }
    render() {
        return (
            <Router>
                <header>
                    <div className="container">
                        <div id="branding">
                            <h1>Durga Prasad</h1>
                            {/* <h1><span className="highlight">Prasad</span> web design</h1> */}
                        </div>

                        <nav>
                            <ul>
                                {menuItems.map(item => {
                                    return <li key={item.key} onClick={() => { this._handleMenuClick(item.name) }} className={this.state.name === item.name ? 'current' : ''}><Link to={item.url}>{item.name}</Link></li>
                                })}
                            </ul>
                        </nav>
                    </div>
                </header>
                <Switch>
                    <Route exact path='/MyBlog' component={Home} />
                    <Route path='/MyBlog/about' component={About} />
                    <Route path='/MyBlog/services' component={Services} />
                </Switch>

            </Router>


        )
    }
}

export default Header